/* We need to add display:inline in order to align the '>>' of the 'read more' link */
.post-excerpt p {
	display:inline;
}

/* 제품 스펙 테이블 */

$primary-color: #008cba;
$gray-color: #ddd;
$medium-up: 600px;
$large-up: 1000px;
$global-radius: 4px;


@mixin heading-font-regular {
  font-family: 'Helvetica', 'Helvetica Neue', 'Arial', sans-serif;;
}

@media screen and (min-width: $large-up) {

	*,*:before,*:after{
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}

	main {
		padding: 2em;  
		float: left;
		width: 100%;
	}

	table {
		border-collapse: collapse;
		width: 100%;
		float: left;
		margin: 0 0 2em;

		thead {
			border-bottom: 3px solid $primary-color;
			th {
				padding: 0.35em 0 .35em;
				font-weight: 400;
				text-align: left;
				@include heading-font-regular;
				font-size: 0.913em;
			}
		}

		tbody {
			border-bottom: 3px solid $primary-color;
			tr {
				border-bottom: 2px solid #ddd;
				td {
				padding: .75em 0;
				@include heading-font-regular;
				font-size: 0.613em;				
				a {
					color: $primary-color;
					text-decoration: none;
					display: inline-block;
					margin: 0 .5em 0 0;
					&:hover, &:active, &:focus {
						color: darken($primary-color, 10%);
						border: none;
					}
				}
				}
			}
		}

		tfoot {
			td {
				padding: 0.35em 0 .35em;

				text-align: left;
				@include heading-font-regular;
				font-size: 0.625em;
			}
		}
  	}	
}

///RWD MIXIN BELOW
@mixin rwd-first {

	*,*:before,*:after{
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}

	main {
		padding: 2em;  
		float: left;
		width: 100%;
	}

	display: block;
  

	tbody {
		border: none;
	}

    tbody, th, td, tr, tfoot {
		display: block; 
    }

    thead {
		display: none;
    }

    tr {
		float: left;
		width: 100%;
		margin: 0 0 1em;
		border: 1px solid $gray-color;
		box-shadow: 0px 2px 5px 0px $gray-color;
		border-radius: $global-radius;
		border-top: 5px solid $primary-color;

		td {
			padding: .5em .5em .5em 50%;
			float:left;
			width: 100%;
			&:before {
				width: 100%;
				display: block;
				float: left;
				padding: .5em .5em .5em 0;
				clear: both;
				margin: -.5em 0 0 -100%;
				@include heading-font-regular;
				font-size: 0.625em;
			}
		}
    }

    tr:nth-of-type(even) {
      //background: $gray-color;
      td {
        &:before {
          
        }
      }
    }
}

@mixin rwd-second {
  display: table;
  border: none;

  tbody {
    border-bottom: 3px solid $primary-color;
  }
  th, td {
    display: table-cell; 
  }

  tr {
    display: table-row;
    border: none;
    border-bottom: 1px solid #eee;
    float: none;
    margin: 0;
    box-shadow: none;

    td {
      padding: .75em 0 0.75em .25em;
      float: none;
      width: auto;

      &:before {
        padding: 0 !important;		
      }
    }
  }

  thead {
    display: table-header-group;
  }

  tbody,tfoot {
    display: table-row-group;
  }

  tr:nth-of-type(even) {
    background:none;
    td {
      
      &:before {
        
      }
    }
  }
}

%responive-tables {
  @include rwd-first;

  @media screen and (min-width: $medium-up) { 
    @include rwd-second;
    
  }
}

%responive-tables-large {
  @include rwd-first;

  @media screen and (min-width: $large-up) { 
    @include rwd-second;
    
  }
}

@mixin responive-tables($headings...) {
  $list: $headings;

  @each $list-headings in $list {
    $i: index($list, $list-headings);
    tbody tr td:nth-of-type(#{$i}):before { 
      content: $list-headings; 
    }
    
    @media screen and (min-width: $medium-up) { 
      tbody tr td:nth-of-type(#{$i}):before { 
        content: ''; 
      }
    }

  }
}

@mixin responive-tables-large($headings...) {
  $list: $headings;

  @each $list-headings in $list {
    $i: index($list, $list-headings);
    tbody tr td:nth-of-type(#{$i}):before { 
      content: $list-headings; 
    }
    
    @media screen and (min-width: $large-up) { 
      tbody tr td:nth-of-type(#{$i}):before { 
        content: ''; 
      }
    }

  }
}

#rwd-table {
	@extend %responive-tables;
	@include responive-tables('제품명', '설명', '제품링크');
  }

#rwd-table-large {
  @extend %responive-tables-large;
  @include responive-tables-large('제품명', 'SPEC', '판매가격','제품링크');
}

